import React, { useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import TimerIcon from '@material-ui/icons/Timer';
import EventNoteIcon from '@material-ui/icons/EventNote';
import ContactsIcon from '@material-ui/icons/Contacts';
import LocalLibraryIcon from '@material-ui/icons/LocalLibrary';
import FlareIcon from '@material-ui/icons/Flare';
import DirectionsBoatIcon from '@material-ui/icons/DirectionsBoat';
import InsertChartIcon from '@material-ui/icons/InsertChart';
import { SwordIcon } from 'src/components/icons'
import { Divider } from '@material-ui/core';
import { Link } from 'gatsby';
import { NestedLists } from 'src/components';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
      maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
    },
    nested: {
      paddingLeft: theme.spacing(4),
    },
  }),
);

function NestedList() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);

  const handleClick = () => {
    setOpen(!open);
  };

  return (
    <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      disablePadding={true}
      subheader={
        <ListSubheader component="div" id="nested-list-subheader">
          Calculators
        </ListSubheader>
      }
      className={classes.root}
    >
      <NestedLists
        topIcon={<SwordIcon />}
        topText={""}
        lists={[{icon: <EventNoteIcon />, text: "", link: "/"},
                {icon: <EventNoteIcon />, text: "", link: "/"}]}
      />
      <NestedLists
        topIcon={<LocalLibraryIcon />}
        topText={""}
        lists={[{icon: <EventNoteIcon />, text: "", link: "/"},
                {icon: <EventNoteIcon />, text: "", link: "/"}]}
      />
      <NestedLists
        topIcon={<FlareIcon />}
        topText={""}
        lists={[{icon: <EventNoteIcon />, text: "", link: "/"},
                {icon: <EventNoteIcon />, text: "", link: "/"}]}
      />
      <ListItem button key='' component={Link} to={'/'}>
        <ListItemIcon><InsertChartIcon /></ListItemIcon>
        <ListItemText primary=''/>
      </ListItem>
      <ListItem button key='' component={Link} to={'/calc/nightmare'}>
        <ListItemIcon><TimerIcon /></ListItemIcon>
        <ListItemText primary=''/>
      </ListItem>
      <ListItem button key='' component={Link} to={'/calc/ship'}>
        <ListItemIcon><DirectionsBoatIcon /></ListItemIcon>
        <ListItemText primary=''/>
      </ListItem>
      <Divider />
      <List
        component="nav"
        aria-labelledby="nested-list-subheader"
        subheader={
          <ListSubheader component="div" id="nested-list-subheader">
            Simulators
          </ListSubheader>
        }
        className={classes.root}
      >
        <ListItem button key=''>
          <ListItemIcon><InboxIcon /></ListItemIcon>
          <ListItemText primary=''/>
        </ListItem>
        <ListItem button key=''>
          <ListItemIcon><InboxIcon /></ListItemIcon>
          <ListItemText primary=''/>
        </ListItem>
        <ListItem button key=''>
          <ListItemIcon><InboxIcon /></ListItemIcon>
          <ListItemText primary=''/>
        </ListItem>
        <ListItem button key=''>
          <ListItemIcon><InboxIcon /></ListItemIcon>
          <ListItemText primary=''/>
        </ListItem>
      </List>
      <Divider />
      <List>
        <ListItem button key='' component={Link} to={'/about/changelog'}>
          <ListItemIcon><EventNoteIcon /></ListItemIcon>
          <ListItemText primary='' />
        </ListItem>
        <ListItem button key='Contact' component={Link} to={'/about/contact'}>
          <ListItemIcon><ContactsIcon /></ListItemIcon>
          <ListItemText primary='Contact us' />
        </ListItem>
        <ListItem button key='Credits' component={Link} to={'/about/credits'}>
          <ListItemIcon><InboxIcon /></ListItemIcon>
          <ListItemText primary='Credits' />
        </ListItem>
      </List>
    </List>
  );
}

export default NestedList;